import "./App.css";
import { Route, Switch } from "react-router-dom";
import Main from "./components/Main";
import Info from "./components/Info";
import * as api from "./api/api";

function App() {
  const hendleRequest = () =>{
    api.getInfo().then((data)=>{
      console.log(data)
    });
  }

  return (
    <div className="App">
      <Switch>
        <Route exact path="/">
          <Main onChange={hendleRequest}/>
        </Route>
        <Route path="/info">
          <Info />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
